.Joke {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-bottom: 1px solid #eeeeee;
  font-weight: 400;
  padding: 1rem;
  /* width: 60%; */
}

.Joke-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 15%;
}

.Joke-text {
  width: 75%;
  font-size: 1.2 rem;
  padding-left: 10%;
}

.Joke-votes {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  text-align: center;
  font-size: 100%;
  border: 3px solid red;
  -webkit-box-shadow: 5px 5px 5px 0px #cec9c9, inset 4px 4px 15px 0px #cec9c9, 5px 5px 15px 5px rgba(23,131,255,0);
          box-shadow: 5px 5px 5px 0px #cec9c9, inset 4px 4px 15px 0px #cec9c9, 5px 5px 15px 5px rgba(23,131,255,0);
}

.fa-arrow-down, .fa-arrow-up {
  font-size: 100%;
  margin: 7%;
  cursor: pointer;
}


